* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  min-height: 100vh;
  min-width: 100vw;
  background-color: rgb(92, 101, 115);
  color: white;
}

button {
  padding: 8px 12px;
  border-radius: 8px;
  border: none;
  outline: none;
}
.todo-app {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2em;
  margin-top: 200px;
}
.todo-input {
  display: flex;
  gap: 2em;
  justify-content: center;
}
.todo-list ul{
  display: flex;
  flex-direction: column;
  gap: 1em;
  padding: 0;
}
.todo-list ul li {
  display: flex;
  justify-content: space-between;
  gap: 2em;
  align-items: center;
  list-style: none;
}

li .todo-item {
  width: 200px;
}
li.active p{
  text-decoration: line-through;
}
.todo-action {
  display: flex;
  gap: 8px;
}